body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.Month-month {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-align-items: stretch;
          align-items: stretch;
  overflow: hidden;
}

.Month-week {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  /* noinspection CssRedundantUnit */
  -webkit-flex: 1 0;
          flex: 1 0; /* the 'redundant' unit is necessary for IE11. */
}

.Month-day {
  /* min-width: 0 ensures the flex layout won't expand to fit its child content, if the child content is too big. 
   * Since we're a calendar layout, our main concern is the calendar days being exactly the same size.
   */
  min-width: 0;
  /* noinspection CssRedundantUnit */
  -webkit-flex: 1 0;
          flex: 1 0; /* the 'redundant' unit is necessary for IE11. */
}

.Month-week-header {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.Month-week-header-weekday {
  /* noinspection CssRedundantUnit */
  -webkit-flex: 1 0;
          flex: 1 0; /* the 'redundant' unit is necessary for IE11. */
}

.App {
  text-align: center;
}

.calendar-day {
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 100%;
}

